import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { HyperLinkMedium, Label } from "../components/textstyling"

const FinalContent = styled.div`
  height: 480px;
  width: 100%;
  grid-area: footer;
`

const FooterItems = styled.div`
  margin-top: 190px;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
  grid-template-areas:
    "... linkone ... ... ..."
    "... linktwo ... ... ..."
    "... linkthree ... ... ..."
    "... menufooter ... ... ...";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 4fr 1fr 1fr 40px;
  }

  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 1fr 1px 1px 30px;
  }
`

const Credits = styled(Label)`
  color: ${props => props.theme.footerblack};
  float: left;
  padding-top: 10px;

  @media only screen and (max-width: 450px) {
    font-size: 8px;
  }
`

const FooterLogo = styled.img`
  width: 50px;
  margin-right: 20px;
  float: left;
`

const LinkToSocial = styled.a`
  grid-area: linkone;
  width: 32px;
`

const SocialFollowFooter = styled.img`
  grid-area: linkone;
  width: 32px;
  margin-right: 20px;
  float: left;
  cursor: pointer;
  margin-bottom: 20px;
`

const FooterCredits = styled.div`
  grid-area: menufooter;
  margin-top: 100px;
`

const LinksToPages = styled(Link)`
  width: 200px;
  grid-area: ${props => props.gridarea};
  margin-bottom: 20px;
  font-size: 14px;
  color: ${props => props.theme.footerblack};

  :hover {
    color: black;
    text-decoration: underline;
  }
`

export const Footer = () => (
  <FinalContent>
    <FooterItems>
      <LinkToSocial href="https://www.instagram.com/32icons/">
        <SocialFollowFooter
          src="../images/globalsvg/ig_logo.svg"
          alt="ICON_ERROR"
        />
      </LinkToSocial>
      <LinksToPages to="/about/" gridarea="linktwo">
        about 32icons
      </LinksToPages>
      <LinksToPages to="404" gridarea="linkthree">
        terms and conditions
      </LinksToPages>
      <FooterCredits>
        <FooterLogo
          src="../images/globalsvg/footer32icon.svg"
          alt="ICON_ERROR"
        />
        <Credits>
          COPYRIGHT ©{new Date().getFullYear()} - 32ICONS — ALL RIGHTS RESERVED
        </Credits>
      </FooterCredits>
    </FooterItems>
  </FinalContent>
)
