import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { HyperLink, HeroTitle } from "../components/textstyling"

const FirstContent = styled.div`
  width: 100%;
  grid-area: header;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
  grid-template-areas: "... logo ... sociallink ...";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 2fr 2fr 2fr 40px;
  }

  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 1fr 3fr 30px;
    grid-template-areas: "... logo sociallink ...";
  }
`
const FirstContentProjectpage = styled.div`
  width: 100%;
  grid-area: header;
  background-image: url(${props => props.projectimage});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
  grid-template-areas:
    "... logo ... sociallink ..."
    "... ... ... ... ..."
    "... herotitle herotitle herotitle ...";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 2fr 2fr 2fr 40px;
  }

  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 1fr 3fr 30px;
    grid-template-areas: "... logo sociallink ...";
  }
`

const GradientOverlay = styled.div`
  background-image: linear-gradient(
      67deg,
      rgba(106, 116, 172, 0.68) 0%,
      rgba(144, 36, 86, 0.21) 100%
    ),
    linear-gradient(0deg, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, 1) 100%),
    linear-gradient(180deg, rgba(0, 0, 0, 0) 73%, rgba(0, 0, 0, 1) 100%);

  height: 100%;
  width: 100%;
  grid-column-start: 1;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 4;
`

const LogoHeader = styled.img`
  height: 50px;
`

const HomepageLink = styled(Link)`
  grid-area: logo;
  margin-top: 60px;
  height: 50px;
  width: 80px;
  @media only screen and (max-width: 450px) {
    margin-top: 30px;
  }
`

const SocialLink = styled.div`
  grid-area: sociallink;
`

const FollowUs = styled.a`
  float: right;
  text-align: right;
  margin-top: 59px;
  cursor: pointer;

  @media only screen and (max-width: 450px) {
    margin-top: 28px;
  }
`

const IgLogoHeader = styled.img`
  height: 32px;
`

const ProjectTitle = styled(HeroTitle)`
  grid-area: herotitle;
  margin-top: 281px;
`

export const Header = () => (
  <FirstContent>
    <HomepageLink to="/">
      <LogoHeader
        src="../images/globalsvg/32icons_logo.svg"
        alt="32Icons logo"
      />
    </HomepageLink>
    <SocialLink>
      <FollowUs href="https://www.instagram.com/32icons/">
        <IgLogoHeader
          src="../images/globalsvg/ig_logo.svg"
          alt="32Icons logo"
        />
      </FollowUs>
    </SocialLink>
  </FirstContent>
)

export const HeaderProjectpage = ({ projectpagetitle, projectimage }) => (
  <FirstContentProjectpage projectimage={projectimage}>
    <GradientOverlay />
    <HomepageLink to="/">
      <LogoHeader
        src="../images/globalsvg/32icons_logo_pp.svg"
        alt="32Icons logo"
      />
    </HomepageLink>
    <SocialLink>
      <FollowUs href="https://www.instagram.com/32icons/">
        <IgLogoHeader
          src="../images/globalsvg/ig_logo_pp.svg"
          alt="32Icons logo"
        />
      </FollowUs>
    </SocialLink>
    <ProjectTitle>{projectpagetitle}</ProjectTitle>
  </FirstContentProjectpage>
)
