import React from "react"
import styled, { ThemeProvider } from "styled-components"
import GlobalStyles from "../components/globalstyles"
import { Header, HeaderProjectpage } from "../components/header"
import { Footer } from "../components/footer"
import { theme } from "../components/theme"

const Container = styled.div`
  display: grid;
  grid-template-columns: 1fr 6fr 1fr;
  grid-template-rows: 200px 1fr 400px;
  grid-template-areas:
    "header header header"
    "... content ..."
    "footer footer footer";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 6fr 40px;
  }
  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 6fr 30px;
    grid-template-rows: 130px 1fr 400px;
  }
`

const ContainerProjectPage = styled.div`
  display: grid;
  grid-template-columns: 1fr 6fr 1fr;
  grid-template-rows: 500px 1fr 400px;
  grid-template-areas:
    "header header header"
    "... content ..."
    "footer footer footer";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 6fr 40px;
  }
  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 6fr 30px;
    grid-template-rows: 130px 1fr 400px;
  }
`

const Main = styled.div`
  grid-area: content;
`

export const Layout = ({ children }) => (
  <>
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <Container>
        <Header />
        <Main>{children}</Main>
        <Footer />
      </Container>
    </ThemeProvider>
  </>
)

export const LayoutProjectpage = ({
  projectpagetitle,
  projectimage,
  children,
}) => (
  <>
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <ContainerProjectPage>
        <HeaderProjectpage
          projectpagetitle={projectpagetitle}
          projectimage={projectimage}
        />
        <Main>{children}</Main>
        <Footer />
      </ContainerProjectPage>
    </ThemeProvider>
  </>
)
